import React,{useEffect} from 'react';
import './common.css';
import { Container, Row, Col } from 'react-bootstrap';
import allcourses from '../../data/allcourses';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

function AllCoursesPage() {
    useEffect(() => {
        window.scrollTo(0, 0);
      }, []);
    return (
        <div style={{marginTop:'7rem'}}>
          
          <Container>
          <div style={{margin:'auto 10%'}}>
          <Row>
                <Col xl={4}>
                    <div className='all-course-box1'>
                      <div className='course-box1-links'>
                        <div style={{display:'flex',alignItems:'center',flexDirection:'column'}}>
                        <h3>Categories</h3>
                        <div style={{background:'red',height:'5px',width:'50px'}}></div>
                        </div> 
                        <div className='course-box1-link'>
                          <p className='box-link'><ArrowForwardIcon className='link-icon' style={{display:'none'}}></ArrowForwardIcon>Programing</p>
                          <p>Programing</p>
                          <p>Programing</p>
                          <p>Programing</p>
                          <p>Programing</p>
                        </div> 
                      </div>
                    </div>
                </Col>
                <Col xl={8}>
                <div className='all-course-box2'></div>
                </Col>
            </Row>
          </div>
           
          </Container>
        </div>
    );
}

export default AllCoursesPage;
